<template>
    <div id="note" class="detail">
        <note-sidebar @update:notes="val => notes = val"></note-sidebar>
        <div id="note-detail" class="note-detail">
            <div class="note-empty" v-show="!curNote.id"></div>
            <div class="note-ct" v-show="curNote.id">
                <div class="note-bar">
                    <span> {{curNote.createdAtFriendly}}</span>
                    <span> {{curNote.updatedAtFriendly}}</span>
                    <span> {{statusText}}</span>
                    <span class="iconfont icon-delete" @click="onDeleteNote"></span>
                    <span class="iconfont icon-fullscreen" @click="isShowPreview = !isShowPreview"></span>
                    <!--<span class="iconfont icon-edit" @click="isShowPreview = isShowPreview"></span>-->
                </div>
                <div class="note-title">
                    <input type="text" v-model:value="curNote.title" @input="onUpdateNote" @keyup="statusText='...'"
                           placeholder="">
                </div>
                <div class="editor">
                    <!--<codemirror v-model="curNote.content" :options="cmOptions" v-show="!isShowPreview" @input="onUpdateNote" @inputRead="statusText='...'"></codemirror>-->
                    <textarea v-show="isShowPreview" v-model:value="curNote.content" @input="onUpdateNote"
                              @keyup="statusText='...'"
                              placeholder=",  markdown "></textarea>
                    <div class="preview markdown-body" v-html="previewContent" v-show="!isShowPreview"></div>
                </div>
            </div>
            <!--<h1>params:{{$route.params.noteId}}</h1>-->
            <!--<h1>notebookId:{{$route.query.notebookId}}</h1>-->
            <!--<h1>noteId:{{$route.query.noteId}}</h1>-->
        </div>
    </div>
</template>
<script>
    import  NoteSidebar from '@/components/NoteSidebar'
    import  _  from  'lodash'
    import MarkdownIt from  'markdown-it'
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'
    import {codemirror} from 'vue-codemirror'
    import 'codemirror/lib/codemirror.css'
    import 'codemirror/mode/markdown/markdown.js'
    import 'codemirror/theme/neat.css'
    //    var MarkdownIt = require('markdown-it'),
    //    md = new MarkdownIt();
    //    var result = md.render('# markdown-it rulezz!');
    let md = new MarkdownIt()

    export  default {
        name: 'NoteDetail',
        components: {
            NoteSidebar
        },
        data(){
            return {
                statusText: '',
                isShowPreview: false
            }
        },
        created() {
            this.checkLogin({path: '/login'})
        },
        computed: {
            ...mapGetters(['notes', 'curNote']),
            previewContent() {
                return md.render(this.curNote.content || '')
            }
        },
        methods: {
            ...mapMutations(['setCurNote']),
            ...mapActions(['updateNote', 'deleteNote', 'checkLogin']),
            onUpdateNote: _.debounce(function () {
                if (this.curNote === null) return
                this.updateNote({noteId: this.curNote.id, title: this.curNote.title, content: this.curNote.content})
                    .then(res => {
                        this.statusText = ''
                    }).catch(err => {
                    this.statusText = ''
                })

            }, 300),
            onDeleteNote() {
                this.deleteNote({noteId: this.curNote.id}).then(() => {
                    this.$router.replace({path: '/note'})
                })
            }
        },
        beforeRouteUpdate (to, from, next) {
            // 
            //  /note/:id /note/1  /note/2 
            //  NoteDetail 
            //  `this`
            this.setCurNote({curNoteId: to.query.noteId})
            //this.curNote = this.notes.find(note => note.id == to.query.noteId) || {}
            next()
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    @import url(../assets/css/note-detail.less);
    /*@import url(//at.alicdn.com/t/font_754780_nw3rw72dqx.css);*/
</style>


